:root {
  --primary-color: #2B6CB0;
  --secondary-color: #F6AD55;
  --background-color: #EDF2F7;
  --text-color: #2D3748;
  --font-family: 'Inter', Arial, sans-serif;
}

.details-page-container {
  font-family: var(--font-family);
  background: radial-gradient(circle, rgba(30, 58, 138, 0.8), rgba(17, 24, 39, 1));
  color: #E2E8F0;
  padding: 40px 20px;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}

.details-layout {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  background: #2D3748;
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  padding: 20px;
}

.space-image-container {
  width: 100%;
  max-width: 600px;
  margin-bottom: 20px;
}

.space-image {
  width: 100%;
  height: auto;
  border-radius: 16px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
}

.space-details-container {
  width: 100%;
  max-width: 600px;
  text-align: left;
  padding: 20px;
}

.space-details-container h1 {
  font-size: 2.2rem;
  color: #F59E0B;
  margin-bottom: 20px;
  font-weight: 700;
}

.space-details-container p {
  font-size: 1rem;
  line-height: 1.6;
  margin-bottom: 10px;
  font-weight: 600;
}

.space-details-container .space-price {
  font-size: 1.5rem;
  color: #F59E0B;
  font-weight: bold;
  margin-top: 20px;
}

.configure-button {
  background: linear-gradient(45deg, #14B8A6, #FBBF24); /* Teal to Gold Gradient */
  color: white;
  padding: 12px 40px;
  border: none;
  cursor: pointer;
  margin-top: 20px;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 600;
  transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
}

.configure-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 15px rgba(20, 184, 166, 0.4);
}

/* Loading and Error Messages */
.loading-message,
.error-message {
  text-align: center;
  color: var(--text-color);
  font-size: 1.2rem;
  font-weight: 600;
  padding: 20px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  background: rgba(246, 173, 85, 0.2); /* Subtle orange background */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Responsive Design */
@media (min-width: 768px) {
  .details-layout {
    flex-direction: row;
    justify-content: space-between;
    padding: 40px;
  }

  .space-image-container {
    margin-right: 40px;
  }

  .space-details-container {
    padding: 20px 40px;
  }
}

@media (min-width: 1200px) {
  .details-layout {
    padding: 60px;
  }

  .space-image-container {
    max-width: 700px;
  }

  .space-details-container {
    max-width: 700px;
  }

  .space-details-container h1 {
    font-size: 2.5rem;
  }

  .configure-button {
    font-size: 1.2rem;
    padding: 14px 50px;
  }
}
