:root {
  --color-primary: #2B6CB0; /* Rich Blue */
  --color-primary-light: #63B3ED; /* Sky Blue */
  --color-secondary: #F6AD55; /* Warm Orange */
  --color-secondary-light: #FBD38D; /* Light Peach */
  --color-accent: #48BB78; /* Green Teal */
  --color-background: #1A202C; /* Light Grayish White */
  --color-card: #FFFFFF; /* Pure White */
  --color-overlay: radial-gradient(circle, rgba(30, 58, 138, 0.8), rgba(17, 24, 39, 1));
  --color-text: #2D3748; /* Deep Slate */
  --color-text-light: #E2E8F0; /* Off-White Text */
  --color-error: #E53E3E; /* Vibrant Red */

  /* Typography */
  --font-primary: 'Inter', sans-serif;
  --font-secondary: 'Merriweather', serif;

  /* Layout */
  --max-width: 1200px;
  --padding: 20px;
  --border-radius: 12px;

  /* Shadows */
  --shadow-default: 0 4px 12px rgba(0, 0, 0, 0.1);
  --shadow-hover: 0 8px 24px rgba(0, 0, 0, 0.2);

  /* Transitions */
  --transition-fast: 0.2s ease-in-out;
  --transition-medium: 0.3s ease-in-out;
  --transition-slow: 0.5s ease-in-out;

  /* Z-Index */
  --z-index-overlay: 10;
}

/* Dark Mode Variables */
@media (prefers-color-scheme: dark) {
  :root {
    --color-background: #7297db;
    --color-card: #2D3748;
    --color-text: #EDF2F7;
    --color-overlay: radial-gradient(circle, rgba(30, 58, 138, 0.8), rgba(17, 24, 39, 1));
  }
}

/* Global Styles */
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-primary);
  background: radial-gradient(circle, rgba(30, 58, 138, 0.8), rgba(17, 24, 39, 1));
  color: var(--color-text);
  line-height: 1.6;
  transition: background-color var(--transition-medium), color var(--transition-medium);
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  display: block;
  border-radius: var(--border-radius);
  transition: transform var(--transition-fast);
}

button {
  font-family: inherit;
  cursor: pointer;
  border: none;
  background: none;
}

/* Container */
.home-page-container {
  padding: 80px var(--padding);
  max-width: var(--max-width);
  margin: 0 auto;
}

/* Center Content */
.home-content-center {
  text-align: center;
}

/* Titles */
.home-title {
  font-family: var(--font-secondary);
  font-size: 3rem;
  color: #F59E0B;
  margin-bottom: 40px;
  position: relative;
  font-weight: 900;
}

.home-title::after {
  content: '';
  width: 100px;
  height: 6px;
  background: linear-gradient(90deg, var(--color-primary), var(--color-secondary));
  display: block;
  margin: 20px auto 0;
  border-radius: 3px;
}

.home-section-title {
  font-family: var(--font-secondary);
  font-size: 2.5rem;
  color: #F59E0B;
  margin-bottom: 30px;
  position: relative;
  font-weight: 700;
}

.home-section-title::after {
  content: '';
  width: 80px;
  height: 4px;
  background: linear-gradient(90deg, var(--color-primary-light), var(--color-secondary-light));
  display: block;
  margin: 15px auto 0;
  border-radius: 2px;
}

/* Messages */
.home-loading,
.home-error-message,
.home-no-spaces {
  font-size: 1.3rem;
  margin: 25px 0;
}

.home-error-message {
  color: var(--color-error);
}

/* Slider Container */
.home-slider-container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 80px;
}

/* Arrow Buttons */
.home-arrow {
  background: var(--color-card);
  border-radius: 50%;
  width: 60px;
  height: 60px;
  box-shadow: var(--shadow-default);
  transition: background var(--transition-medium), transform var(--transition-medium), box-shadow var(--transition-medium);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  color: var(--color-primary);
}

.home-arrow:hover {
  background: linear-gradient(135deg, var(--color-primary), var(--color-secondary));
  color: var(--color-text-light);
  transform: translateY(-5px);
  box-shadow: var(--shadow-hover);
}

.home-arrow:focus {
  outline: 3px solid var(--color-secondary);
  outline-offset: 4px;
}

.home-arrow.disabled {
  opacity: 0.6;
  cursor: not-allowed;
  pointer-events: none;
}

/* Spaces Slider */
.home-spaces-slider {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 30px;
  overflow: hidden;
  width: 100%;
  transition: transform var(--transition-medium) ease-in-out;
}

/* Space Link */
.home-space-link {
  position: relative;
  display: block;
  background: var(--color-card);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-default);
  overflow: hidden;
  transition: transform var(--transition-medium), box-shadow var(--transition-medium);
}

.home-space-link:hover,
.home-space-link:focus-within {
  transform: translateY(-10px);
  box-shadow: var(--shadow-hover);
}

/* Space Image Container */
.home-space-image-container {
  position: relative;
  width: 100%;
  padding-top: 56.25%; /* 16:9 Aspect Ratio */
  overflow: hidden;
}

.home-space-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.home-space-link:hover .home-space-image,
.home-space-link:focus-within .home-space-image {
  transform: scale(1.05);
}

/* Space Overlay */
.home-space-hover-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--color-overlay);
  opacity: 0;
  transition: opacity var(--transition-fast);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: var(--z-index-overlay);
}

.home-space-link:hover .home-space-hover-overlay,
.home-space-link:focus-within .home-space-hover-overlay {
  opacity: 1;
}

.home-space-hover-text {
  color: var(--color-text-light);
  font-size: 1.2rem;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 1.5px;
}

/* Space Info */
.home-space-info {
  padding: 25px;
  text-align: left;
}

.home-space-name {
  font-family: var(--font-secondary);
  font-size: 1.8rem;
  color: #F59E0B;
  margin-bottom: 10px;
  font-weight: 700;
}

.home-space-description {
  font-size: 1rem;
  color: var(--color-text);
  margin-bottom: 15px;
  line-height: 1.5;
  max-height: 4.5em;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

/* Decorative Dots */
.home-space-dots {
  display: flex;
  gap: 6px;
}

.home-space-dot {
  width: 10px;
  height: 10px;
  background-color: var(--color-secondary);
  border-radius: 50%;
  transition: background-color var(--transition-fast);
}

.home-space-link:hover .home-space-dot,
.home-space-link:focus-within .home-space-dot {
  background-color: var(--color-primary);
}

/* Artistic Section */
.home-artistic-section {
  margin-top: 80px;
}

.home-artistic-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 30px;
  transition: all var(--transition-medium) ease-in-out;
}

.home-artistic-card {
  position: relative;
  background: var(--color-card);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-default);
  overflow: hidden;
  transition: transform var(--transition-medium), box-shadow var(--transition-medium);
}

.home-artistic-card:hover,
.home-artistic-card:focus-within {
  transform: scale(1.02);
  box-shadow: var(--shadow-hover);
}

.home-artistic-image-container {
  position: relative;
  width: 100%;
  padding-top: 66.66%; /* 3:2 Aspect Ratio */
  overflow: hidden;
}

.home-artistic-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform var(--transition-fast);
}

.home-artistic-card:hover .home-artistic-image,
.home-artistic-card:focus-within .home-artistic-image {
  transform: scale(1.1);
}

.home-artistic-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--color-overlay);
  color: var(--color-text-light);
  padding: 25px;
  transform: translateY(100%);
  transition: transform var(--transition-medium);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.home-artistic-card:hover .home-artistic-overlay,
.home-artistic-card:focus-within .home-artistic-overlay {
  transform: translateY(0);
}

.home-artistic-name {
  font-family: var(--font-secondary);
  font-size: 1.8rem;
  margin-bottom: 10px;
  font-weight: 900;
}

.home-artistic-description {
  font-size: 1rem;
  line-height: 1.5;
  max-height: 6em;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

/* Responsive Grid */
@media (min-width: 600px) {
  .home-artistic-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 992px) {
  .home-artistic-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .home-slider-container {
    flex-direction: column;
    align-items: center;
  }

  .home-arrow {
    margin: 15px 0;
    width: 50px;
    height: 50px;
    font-size: 1.5rem;
  }

  .home-spaces-slider {
    grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
    gap: 20px;
  }

  .home-space-link {
    margin: 0 auto;
  }

  .home-artistic-grid {
    grid-template-columns: 1fr;
  }

  /* Enhanced Mobile Styles */
  .home-title {
    font-size: 2.5rem;
  }

  .home-section-title {
    font-size: 2rem;
  }

  .home-space-name,
  .home-artistic-name {
    font-size: 1.5rem;
  }

  .home-space-description,
  .home-artistic-description {
    font-size: 0.95rem;
  }

  .home-space-hover-text {
    font-size: 1rem;
  }

  .home-space-dots {
    gap: 4px;
  }

  .home-space-dot {
    width: 8px;
    height: 8px;
  }
}
