.main-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  background: radial-gradient(circle, rgba(30, 58, 138, 0.8), rgba(17, 24, 39, 1)); /* Elegant gradient */
  width: 100%;
  min-height: 100vh;
  padding: 20px;
  font-family: 'Inter', sans-serif; /* Sleek modern font */
}

/* Card Container */
.card-container {
  width: 100%;
  max-width: 700px;
  padding: 30px;
  background: #2D3748;
  border-radius: 20px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: #ffffff;
  animation: fadeIn 0.8s ease-out; /* Smooth entry animation */
}

/* Fade-in Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Title */
.title {
  text-align: center;
  margin-bottom: 25px;
  color: #F59E0B;
  font-size: 2.4rem;
  font-weight: 800;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  position: relative;
  animation: slideIn 0.6s ease-out; /* Title animation */
}

.title::after {
  content: '';
  position: absolute;
  width: 100px;
  height: 4px;
  background: linear-gradient(to right, #fbbf24, #f59e0b); /* Vibrant gradient */
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 5px;
}

/* Slide-in Animation */
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Reservation Count */
.reservation-count {
  text-align: center;
  margin-bottom: 20px;
  color: #ffffff; /* Light gray */
  font-size: 1.2rem;
}

.strong {
  font-weight: 800;
  color: #fbbf24; /* Amber */
}

/* Loader */
.loader {
  text-align: center;
  color: #3b82f6; /* Blue */
  font-size: 1.4rem;
  margin: 20px 0;
}

/* Error Message */
.error {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 15px;
  padding: 15px;
  border-radius: 10px;
  background: rgba(192, 57, 43, 0.2); /* Red background */
  color: #c0392b; /* Red text */
  font-weight: 600;
  font-size: 1rem;
}

.message-icon {
  margin-right: 10px;
  font-size: 1.3rem;
}

/* Reservation List */
.ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

/* Reservation Card */
.card {
  background: linear-gradient(135deg, #2D3748, #2D3748);
  border: 1px solid #e5e7eb;
  border-radius: 15px;
  padding: 25px;
  margin-bottom: 20px;
  color: #ffffff;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card:hover {
  transform: translateY(-10px);
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.15);
}

/* Card Text */
.card p {
  margin-bottom: 10px;
  font-size: 1rem;
  line-height: 1.6;
}

.card p span {
  font-weight: 600;
  color: #F59E0B; /* Dark blue */
}

/* Button Container */
.btn-container {
  display: flex;
  justify-content: flex-end;
  padding-top: 15px;
}

/* Delete Button */
.delete-btn {
  padding: 10px 20px;
  color: #ffffff; /* White text */
  font-size: 1rem;
  font-weight: 600;
  background: linear-gradient(90deg, #c0392b, #e74c3c); /* Gradient red */
  border: none;
  border-radius: 12px;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
}

.delete-btn:hover {
  background: linear-gradient(90deg, #e74c3c, #c0392b); /* Reversed gradient */
  transform: scale(1.05);
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3);
}

.delete-icon {
  margin-right: 8px;
  font-size: 1.2rem;
}

/* No Reservations Message */
.no-reservations {
  text-align: center;
  color: #9ca3af; /* Gray */
  font-size: 1.2rem;
  padding: 25px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.1); /* Transparent background */
}

/* Responsive Styles */
@media (max-width: 768px) {
  .card-container {
    max-width: 90%;
    padding: 20px;
  }

  .title {
    font-size: 1.8rem;
  }

  .card {
    padding: 20px;
  }

  .delete-btn {
    padding: 8px 15px;
    font-size: 0.9rem;
  }
}
