* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --sidebar-bg: linear-gradient(135deg, #1e3c72, #2a5298);
  --sidebar-width: 250px;
  --sidebar-bg-active: rgba(30, 60, 114, 0.95);
  --button-hover-bg: rgba(36, 204, 44, 0.2);
  --button-active-bg: #24cc2c;
  --text-color-side-bar: #ffffff;
  --text-hover-color: #24cc2c;
  --font-family: 'Arial', sans-serif;
  --transition-speed: 0.3s;
}

html,
body {
  height: 100%;
  font-family: var(--font-family);
}

.main-container {
  display: flex;
  min-height: 100vh;
}

.sidebar {
  background: var(--sidebar-bg);
  width: var(--sidebar-width); 
  min-width: var(--sidebar-width);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 30px 20px; /* Increased padding for better spacing */
  transition: transform var(--transition-speed) ease-in-out,
    box-shadow var(--transition-speed) ease-in-out;
  box-shadow: 2px 0 12px rgba(0, 0, 0, 0.1);
  border-right: 1px solid rgba(255, 255, 255, 0.1);
  position: relative;
  z-index: 1000;
}

/* Sidebar Title */
.sidebar h1 {
  margin-bottom: 30px;
  font-size: 26px;
  color: #fbbf24;
  font-weight: bold;
  letter-spacing: 1px;
  text-transform: uppercase;
  position: relative;
}

.sidebar h1::after {
  content: '';
  position: absolute;
  width: 50px;
  height: 3px;
  background-color: #24cc2c;
  bottom: -10px;
  left: 0;
  border-radius: 2px;
}

/* Navigation List */
.sidebar ul {
  list-style: none;
  width: 100%;
}

/* Navigation Items */
.sidebar ul li {
  margin: 15px 0;
}

/* Navigation Buttons */
.sidebar ul li button {
  width: 100%;
  padding: 12px 20px;
  background: transparent;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  font-size: 16px;
  color: var(--text-color-side-bar);
  text-align: left;
  display: flex;
  align-items: center;
  transition: background-color var(--transition-speed) ease,
    transform var(--transition-speed) ease,
    box-shadow var(--transition-speed) ease;
  cursor: pointer;
}

.sidebar ul li button:hover,
.sidebar ul li button:focus {
  background-color: var(--button-hover-bg);
  color: #fbbf24;
  transform: translateX(5px);
  box-shadow: 0 4px 20px rgba(36, 204, 44, 0.3);
}

.sidebar ul li button.active {
  background-color: var(--button-active-bg);
  color: var(--text-color-side-bar);
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
}

/* Icon inside Navigation Buttons */
.iconButton {
  margin-right: 12px;
  width: 20px;
  height: 20px;
  color: var(--text-color-side-bar);
  transition: color var(--transition-speed) ease;
}

.sidebar ul li button:hover .iconButton,
.sidebar ul li button:focus .iconButton {
  color: var(--button-hover-color);
}

.sidebar ul li button.active .iconButton {
  color: var(--text-color-side-bar);
}

/* Toggle Button (Hamburger and Close Icons) */
.icon {
  display: none;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 24px;
  color: var(--text-color-side-bar);
  transition: transform var(--transition-speed) ease;
  position: fixed;
  top: 20px;
  left: 20px;
  z-index: 1100; /* Above the sidebar */
}

.icon:hover {
  transform: rotate(90deg);
}

/* Main Content Area */
.main-content-area {
  flex-grow: 1;
  padding: 30px;
  background-color: #f9f9f9;
  overflow-y: auto;
  transition: margin-left var(--transition-speed) ease;
}

/* Overlay Styling */
.overlay {
  position: fixed;
  top: 0;
  left: 80%;
  width: 20%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  z-index: 500;
  cursor: pointer;
}

/* Mobile Styles */
@media screen and (max-width: 768px) {
  .main-container {
    flex-direction: column;
  }

  .sidebar {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 80%; /* Adjust width for mobile */
    max-width: 300px;
    transform: translateX(-100%);
    z-index: 1000;
    box-shadow: 2px 0 12px rgba(0, 0, 0, 0.4);
  }

  .sidebar.active {
    transform: translateX(0);
    background: var(--sidebar-bg-active); /* Slightly opaque background for better visibility */
  }

  .icon {
    display: block;
  }

  .main-content-area {
    margin-left: 0;
    width: 100%;
    padding: 20px;
  }
}

/* Tablet and Above */
@media screen and (min-width: 769px) {
  .sidebar {
    transform: translateX(0);
    position: relative;
    height: auto;
    background: var(--sidebar-bg);
  }

  .icon {
    display: none;
  }
}

/* Larger Screens */
@media screen and (min-width: 992px) {
  .sidebar h1 {
    font-size: 28px;
  }

  .sidebar ul li button {
    font-size: 18px;
  }

  .iconButton {
    width: 24px;
    height: 24px;
  }
}

@media screen and (min-width: 1400px) {
  .sidebar h1 {
    font-size: 32px;
  }

  .sidebar ul li button {
    font-size: 20px;
  }

  .iconButton {
    width: 28px;
    height: 28px;
  }
}