.register-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: radial-gradient(circle, rgba(30, 58, 138, 0.85), rgba(17, 24, 39, 1)); /* Modern gradient */
  font-family: 'Inter', sans-serif;
  padding: 20px;
  box-sizing: border-box;
}

/* Register Container */
.register-container {
  max-width: 450px;
  width: 100%;
  padding: 30px;
  background: rgba(255, 255, 255, 0.1); /* Frosted glass effect */
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  box-shadow: 0 6px 25px rgba(0, 0, 0, 0.5);
  text-align: center;
  color: #ffffff;
}

/* Title */
.register-container h2 {
  font-size: 2.2rem;
  font-weight: 700;
  margin-bottom: 20px;
  color: #F59E0B; /* Muted Gold for consistency */
}

/* Register Form */
.register-form {
  display: flex;
  flex-direction: column;
  gap: 15px; /* Spacing between form elements */
  text-align: left;
}

/* Form Group */
.register-group {
  display: flex;
  flex-direction: column;
}

label {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 5px;
}

/* Input Fields */
input[type="text"],
input[type="email"],
input[type="password"] {
  width: 100%;
  padding: 12px;
  border: 1px solid rgba(255, 255, 255, 0.4);
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  font-size: 0.9rem;
  outline: none;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

input::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

input:focus {
  border-color: #3B82F6;
  box-shadow: 0 0 6px rgba(59, 130, 246, 0.5);
  background-color: black;
}

/* Register Button */
.register-button {
  background: linear-gradient(90deg, #3B82F6, #14B8A6); /* Consistent Blue-Teal gradient */
  color: #ffffff;
  padding: 14px;
  font-size: 1rem;
  font-weight: 600;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
  text-transform: uppercase;
}

.register-button:hover,
.register-button:focus {
  transform: translateY(-2px);
  box-shadow: 0 6px 18px rgba(59, 130, 246, 0.4);
  background: linear-gradient(90deg, #14B8A6, #3B82F6); /* Reverse gradient */
}

/* Responsive Design */
@media (max-width: 768px) {
  .register-container {
    padding: 20px;
    max-width: 90%;
  }

  .register-container h2 {
    font-size: 1.8rem;
  }

  input[type="text"],
  input[type="email"],
  input[type="password"] {
    font-size: 0.85rem;
    padding: 10px;
  }

  .register-button {
    font-size: 0.9rem;
    padding: 12px;
  }
}

@media (min-width: 1024px) {
  .register-container {
    max-width: 500px;
    padding: 40px;
  }

  .register-container h2 {
    font-size: 2.5rem;
  }

  input[type="text"],
  input[type="email"],
  input[type="password"] {
    font-size: 1rem;
    padding: 14px;
  }

  .register-button {
    font-size: 1.1rem;
    padding: 16px;
  }
}
