.login-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: radial-gradient(circle, rgba(30, 58, 138, 0.8), rgba(17, 24, 39, 1)); /* Elegant gradient */
  font-family: 'Inter', sans-serif;
  padding: 20px; /* Adds padding for smaller screens */
  box-sizing: border-box;
}

/* Login Container */
.login-container {
  max-width: 400px;
  width: 100%;
  padding: 30px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  box-shadow: 0 6px 25px rgba(0, 0, 0, 0.5);
  text-align: center;
  color: #ffffff;
  box-sizing: border-box;
}

/* Title */
.login-container h2 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 10px;
  color: #F59E0B;
}

/* Instructions Styling */
.login-container p {
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.85);
  margin-bottom: 20px;
  line-height: 1.6;
  background: rgba(255, 255, 255, 0.1); /* Subtle frosted glass effect */
  padding: 12px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  text-align: justify;
}

.login-container p strong {
  color: #14b8a6; /* Teal accent for emphasis */
  font-weight: bold;
}

/* Clear Credentials Button */
.clear-credentials-btn {
  background: none;
  border: none;
  color: #3b82f6; /* Blue for action links */
  cursor: pointer;
  font-size: 1rem;
  text-decoration: underline;
  margin-left: 5px;
  font-weight: bold;
  transition: color 0.3s ease;
}

.clear-credentials-btn:hover {
  color: #2563eb; /* Darker blue for hover effect */
  text-decoration: none;
}

/* Login Form */
.login-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  text-align: left;
}

/* Form Group */
.form-group {
  display: flex;
  flex-direction: column;
}

label {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 5px;
}

/* Input Fields */
input[type="email"],
input[type="password"] {
  width: 100%;
  padding: 12px;
  border: 1px solid rgba(255, 255, 255, 0.4);
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  font-size: 0.9rem;
  outline: none;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

input[type="email"]::placeholder,
input[type="password"]::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

input[type="email"]:focus,
input[type="password"]:focus {
  border-color: #3b82f6;
  box-shadow: 0 0 6px rgba(59, 130, 246, 0.5);
  background-color: black;
}

/* Login Button */
.login-button {
  background: linear-gradient(90deg, #3b82f6, #14b8a6);
  color: #ffffff;
  padding: 12px;
  font-size: 1rem;
  font-weight: 600;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-transform: uppercase;
}

.login-button:hover,
.login-button:focus {
  transform: translateY(-2px);
  box-shadow: 0 6px 18px rgba(59, 130, 246, 0.4);
  outline: none;
}

/* Responsive Design */
@media (max-width: 768px) {
  .login-container {
    padding: 20px;
  }

  .login-container h2 {
    font-size: 1.8rem;
  }

  .login-container p {
    font-size: 0.9rem;
    padding: 10px;
    margin-bottom: 15px;
  }

  .clear-credentials-btn {
    font-size: 0.9rem;
  }

  .login-form {
    gap: 12px;
  }

  input[type="email"],
  input[type="password"] {
    font-size: 0.85rem;
    padding: 10px;
  }

  .login-button {
    font-size: 0.9rem;
    padding: 10px;
  }
}

@media (min-width: 1024px) {
  .login-container {
    max-width: 450px;
    padding: 40px;
  }

  .login-container h2 {
    font-size: 2.2rem;
  }

  .login-container p {
    font-size: 1.1rem;
    padding: 14px;
  }

  input[type="email"],
  input[type="password"] {
    font-size: 1rem;
    padding: 14px;
  }

  .login-button {
    font-size: 1.1rem;
    padding: 14px;
  }
}
