.mainContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  background: radial-gradient(circle, rgba(30, 58, 138, 0.8), rgba(17, 24, 39, 1));
  width: 100%;
  min-height: 100vh;
  padding: 20px;
  color: #F9FAFB; /* Light Text */
}

/* Form Wrapper */
.formWrapper {
  background: rgba(255, 255, 255, 0.1);
  padding: 25px 30px;
  border-radius: 15px;
  box-shadow: 0 8px 32px 0 rgba(30, 58, 138, 0.7);
  border: 1px solid rgba(30, 58, 138, 0.18);
  width: 100%;
  max-width: 400px;
}

/* Title */
.title {
  text-align: center;
  margin-bottom: 25px;
  color: #F59E0B;
  font-weight: 700;
  font-size: 1.8rem;
  letter-spacing: 1px;
  text-transform: uppercase;
  position: relative;
}

.title::after {
  content: '';
  position: absolute;
  width: 60px;
  height: 3px;
  background: #F59E0B; /* Gold */
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 2px;
}

/* Form */
.form {
  display: flex;
  flex-direction: column;
}

/* Input Group */
.inputGroup {
  position: relative;
  margin-bottom: 20px;
  width: 100%;
}

/* Input Icons */
.inputIcon {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  color: #F59E0B;
  font-size: 1rem;
}

/* Input Fields */
.inputField,
.selectField,
.textareaField {
  width: 100%;
  padding: 12px 12px 12px 40px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid #374151;
  border-radius: 10px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 0.95rem;
  outline: none;
  transition: background 0.3s ease, box-shadow 0.3s ease;
}

/* Textarea Specific */
.textareaField {
  resize: vertical;
  min-height: 80px;
}

/* Floating Labels */
.label {
  position: absolute;
  top: 50%;
  left: 40px;
  transform: translateY(-50%);
  background: transparent;
  padding: 0 5px;
  color: #F3F4F6;
  font-size: 0.9rem;
  pointer-events: none;
  transition: all 0.3s ease;
}

/* Focused and Filled States */
.inputField:focus + .label,
.inputField:not(:placeholder-shown) + .label,
.selectField:focus + .label,
.selectField:not(:placeholder-shown) + .label,
.textareaField:focus + .label,
.textareaField:not(:placeholder-shown) + .label {
  top: -10px;
  left: 35px;
  color: #F3F4F6;
  font-size: 0.8rem;
}

/* Input Focus */
.inputField:focus,
.selectField:focus,
.textareaField:focus {
  border-color: #3b82f6;
  box-shadow: 0 0 6px rgba(59, 130, 246, 0.5);
  background-color: black;
}

/* Submit Button */
.submitBtn {
  background: linear-gradient(45deg, #14B8A6, #FBBF24); /* Teal to Gold Gradient */
  color: #FFFFFF;
  border: none;
  border-radius: 10px;
  padding: 14px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  margin-top: 10px;
}

.submitBtn:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 15px rgba(20, 184, 166, 0.4);
}

/* Success and Error Messages */
.successMessage,
.errorMessage {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  padding: 10px;
  border-radius: 8px;
  font-weight: 500;
  font-size: 0.95rem;
}

.successMessage {
  background: rgba(20, 184, 166, 0.2); /* Teal Background */
  color: #14B8A6; /* Teal Text */
}

.errorMessage {
  background: rgba(192, 57, 43, 0.2); /* Red Background */
  color: #C0392B; /* Red Text */
}

/* Responsive Design */
@media screen and (max-width: 768px) {
  .formWrapper {
    padding: 20px 25px;
  }

  .title {
    font-size: 1.5rem;
  }

  .submitBtn {
    padding: 12px;
    font-size: 0.95rem;
  }

  .inputIcon {
    font-size: 0.9rem;
  }
}
