/* Container */
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background: radial-gradient(circle, #1e3a8a 0%, #111827 100%);
  font-family: 'Inter', sans-serif;
  padding: 15px;
  box-sizing: border-box;
}

.overlay {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  box-shadow: 0 6px 25px rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  width: 100%;
  max-width: 400px; /* Optimized for smallest screens */
  padding: 15px;
  color: #ffffff;
  animation: fadeIn 0.5s ease-out;
  box-sizing: border-box;
}

/* Fade-in Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Title */
.title {
  text-align: center;
  font-size: 1.8rem; /* Adjusted for smaller screens */
  font-weight: 700;
  color: #F59E0B;
  margin-bottom: 30px;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  position: relative;
}

.title::after {
  content: '';
  position: absolute;
  bottom: -6px;
  left: 50%;
  transform: translateX(-50%);
  width: 50px;
  height: 3px;
  background: linear-gradient(90deg, #14b8a6, #3b82f6);
  border-radius: 2px;
}

/* Form */
.form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/* Input, Select, and Textarea Fields */
.inputField,
.selectField,
.textareaField {
  background: rgba(255, 255, 255, 0.15);
  border: 1px solid rgba(255, 255, 255, 0.4);
  border-radius: 8px;
  padding: 8px;
  font-size: 0.85rem;
  color: #ffffff;
  transition: all 0.3s ease;
  box-sizing: border-box;
}

.inputField::placeholder,
.textareaField::placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.inputField:focus,
.selectField:focus,
.textareaField:focus {
  border-color: #3b82f6;
  box-shadow: 0 0 6px rgba(59, 130, 246, 0.5);
  background-color: black;
}

.textareaField {
  resize: vertical;
  min-height: 60px;
  max-height: 100px;
}

/* Submit Button */
.submitBtn {
  background: linear-gradient(45deg, #14B8A6, #FBBF24);
  border: none;
  color: #ffffff;
  font-size: 0.9rem;
  font-weight: 600;
  padding: 10px;
  border-radius: 8px;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-transform: uppercase;
  box-sizing: border-box;
}

.submitBtn:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 15px rgba(20, 184, 166, 0.4);
}

/* Success and Error Messages */
.successMessage,
.errorMessage {
  font-size: 0.8rem; /* Compact size for smaller screens */
  font-weight: 500;
  text-align: center;
  margin-top: 10px;
  padding: 6px 10px;
  border-radius: 8px;
}

.successMessage {
  background: rgba(46, 204, 113, 0.15);
  color: #2ecc71;
  border: 1px solid rgba(46, 204, 113, 0.4);
}

.errorMessage {
  background: rgba(231, 76, 60, 0.15);
  color: #e74c3c;
  border: 1px solid rgba(231, 76, 60, 0.4);
}

/* Fully Responsive Design */
@media (max-width: 768px) {
  .overlay {
    padding: 10px;
    max-width: 95%;
  }

  .title {
    font-size: 1.4rem;
  }

  .inputField,
  .selectField,
  .textareaField {
    font-size: 0.8rem;
    padding: 8px;
  }

  .submitBtn {
    font-size: 0.85rem;
    padding: 8px;
  }
}

@media (max-width: 480px) {
  .overlay {
    padding: 8px;
    max-width: 95%;
  }

  .title {
    font-size: 1.3rem;
    margin-bottom: 10px;
  }

  .form {
    gap: 8px;
  }

  .inputField,
  .selectField,
  .textareaField {
    font-size: 0.75rem;
    padding: 6px;
  }

  .submitBtn {
    font-size: 0.8rem;
    padding: 6px;
  }

  .successMessage,
  .errorMessage {
    font-size: 0.75rem;
    padding: 5px;
  }
}
